}



.search-box {
  width: 100%;

}

.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 15px;
  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 15vh 15vh 15vh 15vh;
  margin-top: -20px;

  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);

}

.search-box .search-bar:focus {
  background-color: rgba(255, 255, 255, 0.75);

}

.location-box .location {
  color: #fff;
  margin-top: 4vh;
  font-size: 4vh;
  font-weight: 500;
  text-align: center;
  text-shadow: 0.2vh 0.2vh rgba(162, 162, 163, 0.5);
}

.location-box .date {
  color: #fff;
  margin-top: 0.5vh;
  font-size: 2.5vh;
  text-align: center;
  font-style: italic;
  font-weight: 500;

}

.weather-box {
  text-align: center;

}

.weather-box .temp {
  position: relative;
  font-size: 5vh;

  font-weight: 900;
  display: inline-block;
  margin: 4vh auto;
  padding: 2vh 3.5vh;
  
  background-color: rgb(213, 208, 208);
  border-radius: 2vh;
  text-shadow: 0.5vh 0.5vh rgb(253, 253, 255);
  box-shadow: 0.6vh 0.6vh rgb(249, 246, 246);
}

.weather-box .weather {
  color: white;
  font-size: 3vh;
  font-weight: 700;
  text-shadow: 0.2vh 0.2vh rgba(161, 161, 166, 0.5);

}

#weatheradj {
  font-size: 1vw;
  color: aqua;
  font-weight: 700;
  font-style: italic;
  margin-right: 2vh;
}

.navbar {
  margin-top: 2vh;
  background-color: black;
  border: solid rgb(158, 206, 24) 0.5vh;
  border-radius: 8vh;
  color: whitesmoke;
  font-style: oblique;
  text-align: center;
  font-size: 1vw;
  margin-left: 0vh;
  margin-right: 0vh;
  padding: 1vh 1vh;
  opacity: 0.7;
}



